import React, {  useEffect, useState } from 'react';
import { getOther } from '../redux/actions/app'
import Title from '../component/Title'
import Blogs from '../component/Blogs'
import Error from '../component/Error'

const Main = React.memo(
    (props) =>{
        let [blog, setBlog] = useState([]);
        document.title = 'Блог'
        useEffect(async ()=>{
            let data = await getOther({name: 'Блог'})
            await setBlog(data)
            if(window.rerun!=undefined)
                await window.rerun()
        },[]);
        return (
            <>
            <Title title="Блог"/>
            {blog.length>1?

                blog.map((element, idx) => {
                    if(idx!==0&&(idx+1)%2===0) {
                        return <Blogs history={props.history} items={[blog[idx-1], blog[idx]]}/>
                    } else if(idx+1===blog.length) {
                            return <Blogs history={props.history} items={[blog[idx]]}/>
                    }
                })
                :blog.length>0?
                    <Blogs history={props.history} items={[blog[0]]}/>
                :<Error/>
            }
            </>
        );
    })

export default Main;
